* {
    margin: 0;
    padding: 0;
}

.navclass {
    text-decoration: none;

}

.logo {
    display: flex;
    justify-content: space-around;
    background: #a78003;
    height: 70px;
    padding-top: 20px;
    

}

.logo h1 {
    font-size: 30px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.logo h1  span{
    color: #ce0000;
}
.Nav {
    display: flex;
    margin-top: 15px;



}


.Nav a {
    margin-right: 10px;
    text-decoration: none;
    font-size: 18px;


}